* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
}

main {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
}

header {
  display: flex;
  gap: 8px;
  height: 40px;
  padding: 8px 1rem;
  background-color: #1e1e2e;
  align-items: center;
}

header > figure {
  display: flex;
  gap: 4px;
  color: aliceblue;
  align-items: center;
}

header > figure > img {
  width: 100%;
  max-width: 32px;
  height: 100%;
}

footer {
  width: 100%;
  background-color: #1e1e2e;
  color: white;
  height: 40px;
  padding: 8px 1rem;
  text-align: center;
}

/* Style the charts */
/* main .chart {
  width: 50%;
  margin: 20px;
  height: 800px;
} */

main {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 20px;
  max-height: max(400px, calc(100vh - 80px));
  min-height: max(400px, calc(100vh - 80px));
  max-width: 100vw;
  min-width: 100vw;
  flex-grow: 1;
}

main > div {
  position: relative;
  height: 100%;
}

@media (orientation: landscape) {
  main {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
  }
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
